//
// Footer
//

footer.global-footer {
    background: $black;
    @include make-inverse();
    padding: 40px 0 15px;
    @include media(tablet) {
        padding: 65px 0 45px;
    }
    @include media(desktop) {
        padding: 85px 0;
    }
    svg {
        display: block;
        max-width: 100%;
        max-height: 100%;
        .st0 {
            fill: $white;
        }
        .st1 {
            fill: $white;
        }
    }

    .footer-navigation {
        margin-top: 30px;
        @include media(desktop) {
            margin-top: 0;
            text-align: center;
        }
        ul.menu {
            list-style: none;
            > li.menu-item {
                display: block;
                vertical-align: top;
                margin-bottom: 10px;
                @include media(desktop) {
                    display: inline-block;
                    margin-right: 40px;
                    margin-bottom: 0;
                }
                a {
                    font-size: 16px;
                    color: $white;
                }
                ul.sub-menu {
                    margin-top: 20px;
                    display: block;
                    list-style: none;
                }
            }
        }
    }
}


