//
// Resources
//

body.post-type-archive-resource, body.tax-resource_type {
    .global-hero {
    	padding: 50px 0;
    }
    .card {
    	margin-bottom: 40px;
    	.resource-thumbnail {
    		height: 100px;
    		background-size: cover;
    		background-position: center center;
    	}
    	.content-wrapper {
    		padding: 20px 40px;
    	}
    	.resource-meta {
    		margin-bottom: 10px;
    		span + span {
    			float: right;
    		}
    	}
    	.resource-link {
    		font-weight: $weight-bold;
    		margin-top: 10px;
    		text-transform: uppercase;
    		display: inline-block;
    	}
    }
}


