
//
// Header
//

.navbar {
  background: transparent;
  height: 100px;
  padding: 0;
  z-index: 1000;
  width: 100%;
  position: relative;
  .navbar-brand {
      height: 45px;
      line-height: 100px;
      width: 268px;
      font-size: 0;
      padding: 0;
      z-index: 1005;
      svg {
          display: block;
          max-width: 100%;
          max-height: 100%;
          .st0 {
              fill: $brand-primary;
          }
          .st1 {
              fill: $brand-secondary;
          }
      }
  }
  .primaryNav-wrapper {
      .menu-item {
          margin: 0 20px;
          .dropdown-toggle::after {
              display: none;
          }
          @include media(desktop) {
            margin: 0 10px;
          }
          a {
              color: $brand-primary;
              line-height: 50px;
              &:hover {
                  color: $brand-primary-darken;
              }
          }
          &.active, &.current_page_item{
              a {
                  color: $brand-primary-darken;
                  &:hover {
                      color: $brand-primary;
                  }
              }
          }
          &.nav-cta {
              border: none;
      a {
        @extend .btn;
        @extend .btn-primary;
              }
          }
          &:last-of-type {
            margin-right: 0;
          }
      }
  }
}


