//
// Variables
//

/////////////////////////////////
// Colors
$white: #fff;
$black: #000;

$blue: #3a8add;
$blue-darken: darken($blue, 10%);
$blue-light: lighten($blue, 10%);

$green: #00c83c;
$green-darken: darken($green, 5%);
$green-light: lighten($green, 5%);

$orange: #f0ad4e;
$orange-darken: darken($orange, 5%);
$orange-light: lighten($orange, 5%);

$yellow: #E2C01A;
$yellow-darken: darken($yellow, 5%);
$yellow-light: lighten($yellow, 5%);

$teal: #53B9C1;
$teal-darken: darken($teal, 5%);
$teal-light: lighten($teal, 5%);

$purple: #403059;
$purple-darken: darken($purple, 5%);
$purple-light: lighten($purple, 5%);

$red: #e51616;

$gray-dark: #29262E;
$gray: #666666;
$gray-light: #eeeeee;
$gray-lighter: $gray-light;
$gray-lightest: #F9F9F9;

$gray-border: $gray-lighter;
$body-color: $gray;

$brand-primary: $blue; // Primary Brand Color
$brand-primary-darken: darken($brand-primary, 10%);
$brand-primary-lighten: lighten($brand-primary, 10%);
$brand-secondary: $green; // Secondary Brand Color
$brand-secondary-darken: darken($brand-secondary, 5%);
$brand-secondary-lighten: lighten($brand-secondary, 5%);
$brand-tertiary: $teal; // Tertiary Brand Color
$brand-tertiary-darken: darken($brand-tertiary, 10%);
$brand-tertiary-lighten: lighten($brand-tertiary, 10%);

$brand-success: $green;
$brand-info: $teal;
$brand-warning: $yellow;
$brand-danger: $red;
$brand-inverse: $gray-dark;

$twitter: #1ca1f2;
$linkedin: #0077b5;


/////////////////////////////////
// Typography
$font-family-body: sans-serif;
$font-family-headline: $font-family-body;

// Font Weights
$weight-black: 900;
$weight-bold: 700;
$weight-semibold: 500;
$weight-normal: 400;
$weight-light: 300;
$weight-lightest: 200;

$border-color: red;
$font-weight-normal: 400;


