//
// Blog
//

.global-hero {
  padding: 50px 0;
}

.card {
  margin-bottom: 40px;
}

.content-wrapper {
  padding: 20px 40px;
}

.post-link {
  font-weight: $weight-bold;
  margin-top: 10px;
  text-transform: uppercase;
  display: inline-block;
}

.post-meta {
  margin-bottom: 10px;
  ul {
          list-style: none;
      }
}

.post-thumbnail {
  height: 150px;
  background-size: cover;
  background-position: center center;
}


