/*
 * Nylon CSS Partial 
 * Typography: Setting up global font-related properties for all the elements
*/

a {
  text-decoration: none;
  color: $brand-primary;
  &:hover,
  &:focus {
    text-decoration: none;
  }

  &:active {
    color: $brand-primary-darken;
  }
}

body {
  font-family: $font-family-body;
}

b,
strong {
  font-weight: $weight-bold;
}

i,
em {
  font-style: italic;
}

img {
  max-width: 100%;
}

blockquote {
  color: $body-color;
  margin: 30px 0;
  padding: 10px 0 0 40px;
  box-sizing: border-box;
  display: block;
  width: 100%;
  border-left: 4px solid $brand-primary;
  .byline {
    font-size: 16px;
    font-weight: $weight-light;
    display: block;
  }
}

p.lead {
  @include make-lead;
}

.fabric-icon {
  color: $brand-tertiary;
  font-size: 26px;
  display: inline-block;
  vertical-align: middle;
}

.content-styled {
  a {
    color: $brand-primary;
    @include transition();

    &:hover {
      color: $brand-primary-darken;
      text-decoration: none;
    }

    &:active {
      color: $brand-primary-lighten;
      text-decoration: none;
    }
  }

  ul {
    list-style-type: circle;
    margin-left: 1em;

    li {
      margin: 1em 0;
    }
  }

  ol {
    counter-reset: item;
    padding: 0;
    li {
      counter-increment: item;
      list-style: none inside;
      line-height: 30px;
      margin: 1em 0;
      padding-left: 50px;
      position: relative;

      &:before {
        background: $brand-primary;
        border-radius: 50%;
        color: $white;
        content: "" counter(item) "";
        display: inline-block;
        height: 30px;
        left: 0;
        line-height: 30px;
        position: absolute;
        text-align: center;
        width: 30px;
      }
    }
  }

  hr {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    width: 100%;
    height: 1px;
    background-color: $border-color;
  }

  img {
    &.alignleft {
      float: left;
      margin-right: 20px;
      margin-bottom: 20px;
    }
    &.alignright {
      float: right;
      margin-left: 20px;
      margin-bottom: 20px;
    }
    &.aligncenter {
      clear: both;
      float: none;
      margin: 0 auto;
    }
  }

  + .btn,
  + p {
    margin-top: 20px;
  }

  ul {
    display: block;
    list-style: none;
    margin-bottom: 0;
  }
}

p {
  font-family: $font-family-body;
  font-weight: $font-weight-normal;
  font-size: 16px;
  line-height: 1.5em;
  color: $body-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  letter-spacing: 0;
  color: $body-color;
  margin-bottom: 1.2rem;
}

h1 {
  font-family: $font-family-headline;
  font-weight: $weight-bold;
  font-size: 1.875rem;
  line-height: 1em;

  @include media(tablet) {
    font-size: 3.75rem;
    line-height: 1em;
  }
  @include media(desktop) {
    font-size: 4.5rem;
    line-height: 1em;
  }
}

h2 {
  font-size: 1.5rem;
  line-height: 1.25em;

  @include media(tablet) {
    font-size: 1.875rem;
    line-height: 1em;
  }
  @include media(desktop) {
    font-size: 2.25rem;
    line-height: 1em;
  }
}

h3 {
  font-size: 1.313rem;
  line-height: 1.333em;

  @include media(tablet) {
    font-size: 1.5rem;
    line-height: 1.25em;
  }
  @include media(desktop) {
    font-size: 1.875rem;
    line-height: 1em;
  }
}

h4 {
  font-size: 1.125rem;
  line-height: 1.333em;

  @include media(tablet) {
    font-size: 1.313rem;
    line-height: 1.333em;
  }
  @include media(desktop) {
    font-size: 1.5rem;
    line-height: 1em;
  }
}

h5 {
  font-size: 1.125rem;
  line-height: 1.333em;

  @include media(tablet) {
    font-size: 1.313rem;
    line-height: 1.333em;
  }
}

h6 {
  font-size: 1.125rem;
  line-height: 1.333em;
}

ol {
  li {
    &:before {
      font-weight: 500;
    }
  }
}



