//
// Global Form Styles
//

input[type="text"], input[type="email"], input[type="phone"], input[type="tel"], textarea, select {
    &.error {
        border-color: $brand-warning;
    }
    display: block;
    width: 100%;
    border: 1px solid $gray-lighter;
    font-size: 14px;
    line-height: 24px;
    box-sizing: border-box;
    padding: 14px 20px;
    color: $gray;
    background-color: transparent;
    border-radius: 0px;
    -webkit-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    font-family: $font-family-body;
    margin-bottom: 10px;
    background-color: $white;
    &:focus {
        border-color: $gray;
        outline: none;
        box-shadow: none;
    }
}
input[type="text"], input[type="email"], input[type="phone"], input[type="tel"], select {
    height: 50px;
    &::-webkit-input-placeholder {color:#999;opacity:1;}
    &::-moz-placeholder          {color:#999;opacity:1;} /* Firefox 19+ */
    &:-moz-placeholder           {color:#999;opacity:1;} /* Firefox 18- */
    &:-ms-input-placeholder      {color:#999;opacity:1;}
}
select {
    background-image: url('../img/select-arrow.svg');
    background-size: 16px 9px;
    background-repeat: no-repeat;
    background-position: center right 20px;
    line-height: 1em;
    cursor: pointer;
    -webkit-appearance:none;
    -moz-appearance:none;
    -ms-appearance:none;
    appearance:none;
}
input[type="checkbox"], input[type="radio"] {
    margin-right: 4px;
    vertical-align: top;
    margin-top: 5px;
}


//
// Gravity Forms Specific Styling
//
.validation_error {
    color: $brand-warning;
}
.gform_body {
    ul {
        margin: 0;
    }
    .gf_name_has_2 {
        display: flex;
        justify-content: space-between;
        .name_first, .name_last {
            width: 48%;
        }
    }
    .gfield_description {
        &.validation_message {
            color: $brand-warning;
        }
    }
}


